// 게시글 상단
import React from 'react'
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image'

import styled from 'styled-components'

interface PostHeadProps {
  page: string
  date?: string
  startDate?: string
  endDate?: string
  title: string
  subTitle?: string
  categories?: string[]
  team?: string[]
  link?: string[]
  gatsbyImageData?: IGatsbyImageData
}

export default function PostHead({
  page,
  title,
  subTitle,
  date,
  startDate,
  endDate,
  categories,
  team,
  link,
  gatsbyImageData,
}: PostHeadProps) {
  return (
    <>
      {page === 'project' ? (
        <PostHeadContainer>
          <ImageWrapper>
            {gatsbyImageData && (
              <GatsbyImage image={gatsbyImageData} alt="" objectFit="contain" />
            )}
          </ImageWrapper>
          <InfoContainer>
            <DateWrapper>
              <Date>
                {startDate} ~ {endDate}
              </Date>
            </DateWrapper>
            <TitleWrapper>{title}</TitleWrapper>
            <span>{subTitle}</span>
            <CategoryWrapper>
              {categories?.map((v, i) => (
                <Category key={i}>{v}</Category>
              ))}
            </CategoryWrapper>
            <S_TeamWrapper>
              <S_Role>{team && team[0].slice(0, -1)}</S_Role>
              <S_Line>|</S_Line>
              <S_TeamList>
                {team?.map((v, i) => (
                  <span key={i}>{v}</span>
                ))}
              </S_TeamList>
            </S_TeamWrapper>
            <S_LinkContainer>
              <S_Icon
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
                onClick={() => window.open(link && link[1])}
              >
                <path
                  d="M12 .297c-6.63 0-12 5.373-12 12 0 5.303 3.438 9.8 8.205 11.385.6.113.82-.258.82-.577 0-.285-.01-1.04-.015-2.04-3.338.724-4.042-1.61-4.042-1.61C4.422 18.07 3.633 17.7 3.633 17.7c-1.087-.744.084-.729.084-.729 1.205.084 1.838 1.236 1.838 1.236 1.07 1.835 2.809 1.305 3.495.998.108-.776.417-1.305.76-1.605-2.665-.3-5.466-1.332-5.466-5.93 0-1.31.465-2.38 1.235-3.22-.135-.303-.54-1.523.105-3.176 0 0 1.005-.322 3.3 1.23.96-.267 1.98-.399 3-.405 1.02.006 2.04.138 3 .405 2.28-1.552 3.285-1.23 3.285-1.23.645 1.653.24 2.873.12 3.176.765.84 1.23 1.91 1.23 3.22 0 4.61-2.805 5.625-5.475 5.92.42.36.81 1.096.81 2.22 0 1.606-.015 2.896-.015 3.286 0 .315.21.69.825.57C20.565 22.092 24 17.592 24 12.297c0-6.627-5.373-12-12-12"
                  fill="currentColor"
                />
              </S_Icon>
            </S_LinkContainer>
          </InfoContainer>
        </PostHeadContainer>
      ) : (
        <PostHeadContainer>
          <DateWrapper>
            <Date>{date}</Date>
          </DateWrapper>
          <TitleWrapper>{title}</TitleWrapper>
        </PostHeadContainer>
      )}
    </>
  )
}

const S_Icon = styled.svg`
  width: 36px;
  height: 36px;
  color: ${props => props.theme.colors.textColor};
  cursor: pointer;
`

const S_LinkWrapper = styled.div`
  width: 36px;
  height: 36px;
  background-color: ${props => props.theme.colors.tabColor};
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
`

const S_LinkContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  margin-top: 48px;
`

const S_TeamList = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
`

const S_Line = styled.p`
  color: rgb(93, 98, 103);
`

const S_Role = styled.div`
  color: ${props => props.theme.colors.ownColor};
`

const S_TeamWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  font-weight: 500;
  font-size: 14px;

  @media ${props => props.theme.device.mobile} {
    font-size: 12px;
  }
`

const Category = styled.li`
  list-style: none;
  font-size: 14px;
  line-height: 1.5;
  padding: 2px 6px;
  border-radius: 4px;
  color: ${props => props.theme.colors.textColor};
  background: ${props => props.theme.colors.tabColor};
  white-space: nowrap;
  text-overflow: ellipsis;

  @media ${props => props.theme.device.mobile} {
    font-size: 12px;
  }
`

const CategoryWrapper = styled.ul`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin: 24px 0 8px;
`

const ImageWrapper = styled.div`
  width: 100%;
  height: 223px;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
`

const TitleWrapper = styled.p`
  margin-top: 12px;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.38;

  @media screen and (min-width: 767px) {
    margin-top: 24px;
    font-size: 42px;
    line-height: 1.29;
    letter-spacing: -0.8px;
  }

  @media screen and (min-width: 1439px) {
    font-size: 46px;
    line-height: 1.3;
    letter-spacing: -1px;
  }
`

const Date = styled.span`
  font-size: 13px;
  color: ${props => props.theme.colors.postDateColor};
  letter-spacing: -0.2px;

  @media screen and (min-width: 767px) {
    font-size: 18px;
    letter-spacing: -0.5px;
  }
`

const DateWrapper = styled.div`
  line-height: 20px;
`

const InfoContainer = styled.div`
  margin-top: 48px;
`

const PostHeadContainer = styled.div`
  max-width: 364px;
  margin: 0 auto;
  padding: 0 24px;
  text-align: center;

  @media screen and (min-width: 767px) {
    max-width: 630px;
    padding: 0;
  }

  @media screen and (min-width: 1439px) {
    max-width: 816px;
    padding: 0;
  }
`
